import { useEffect } from "react";
import { Link, NavLink, Outlet, ScrollRestoration, useLocation } from "react-router-dom";
import "preline/preline";
import { IStaticMethods } from "preline/preline";
import FiltersOffcanvas from "../components/filters-offcanvas";
import { Album, House, Info, MenuIcon, SearchIcon, SlidersHorizontal } from "lucide-react";
import { useFiltersContext } from "../providers/filters";
import MenuOffcanvas from "../components/menu-offcanvas";
import { ModalCardDetailNumbered, ModalCardDetailProductType, ModalInfoHeader } from "../components/modals";
import SearchModal from "../components/search/search-modal";
import { useClassName } from "../hooks/useClassName";

declare global {
  interface Window {
    HSStaticMethods: IStaticMethods;
  }
}

function Root() {
  const location = useLocation();

  const { filtering } = useFiltersContext();

  useEffect(() => {
    window.HSStaticMethods.autoInit();
  }, [location.pathname]);

  const headerClassName = useClassName([
    "h-16 bg-zinc-950",
    "md:fixed md:left-0 md:right-0 md:z-50 md:bg-transparent",
    location.pathname === "/" || location.pathname === "/collections" || location.pathname === "/changelog" || location.pathname === "/contribute" ? "" : "max-md:hidden",
  ]);

  return (
    <>
      <div className={headerClassName}>
        <div className="2xl:container mx-auto h-16">
          <div className="flex items-center justify-between gap-2 px-4 h-16 md:bg-zinc-950/85 md:backdrop-blur-sm">
            <Link to="/" className="logo">
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 380 88" className="w-[138px] h-8 mt-1">
                <path
                  fillRule="evenodd"
                  fill="rgb(255, 24, 82)"
                  d="M209.123,30.851 L198.235,30.851 L200.276,25.116 L189.680,25.116 L187.687,30.851 L176.799,30.851 L183.653,11.992 C184.106,10.825 184.884,9.910 185.986,9.245 C187.087,8.582 188.416,8.249 189.972,8.249 L217.143,8.249 L209.123,30.851 ZM193.666,13.985 L191.673,19.429 L202.269,19.429 L204.262,13.985 L193.666,13.985 ZM161.342,30.851 L154.634,19.526 L150.551,30.851 L139.858,30.851 L148.024,8.249 L158.766,8.249 L165.425,19.526 L169.508,8.249 L180.202,8.249 L172.036,30.851 L161.342,30.851 ZM124.352,30.851 L126.393,25.116 L115.797,25.116 L113.804,30.851 L102.916,30.851 L109.770,11.992 C110.223,10.825 111.001,9.910 112.103,9.245 C113.204,8.582 114.533,8.249 116.089,8.249 L143.260,8.249 L135.240,30.851 L124.352,30.851 ZM119.783,13.985 L117.790,19.429 L128.386,19.429 L130.379,13.985 L119.783,13.985 ZM96.014,13.887 L90.375,30.851 L79.876,30.851 L85.515,13.887 L75.015,13.887 L76.619,8.249 L107.291,8.249 L104.860,13.887 L96.014,13.887 ZM55.670,30.851 L64.127,8.249 L74.043,8.249 L65.537,30.851 L55.670,30.851 ZM37.782,30.851 L40.553,23.317 L29.276,23.317 L26.505,30.851 L15.228,30.851 L24.366,5.721 C24.917,4.199 25.832,2.992 27.113,2.100 C28.393,1.209 29.842,0.763 31.463,0.763 L60.044,0.763 L49.108,30.851 L37.782,30.851 ZM34.720,8.298 L31.998,15.832 L43.275,15.832 L45.997,8.298 L34.720,8.298 Z"
                />
                <path
                  fillRule="evenodd"
                  fill="rgb(255, 255, 255)"
                  d="M374.761,52.004 L329.900,52.004 L327.672,58.687 L362.406,58.687 C364.837,58.687 366.895,59.464 368.583,61.016 C370.270,62.570 370.844,64.426 370.305,66.586 L364.634,79.953 C364.093,82.249 362.558,84.139 360.026,85.624 C357.495,87.109 354.574,87.851 351.267,87.851 L295.166,87.851 L299.622,75.497 L344.482,75.497 L345.596,69.927 L311.976,69.927 C309.612,69.927 307.587,69.152 305.900,67.598 C304.212,66.046 303.637,64.190 304.179,62.029 L309.748,48.560 C310.288,46.333 311.774,44.477 314.204,42.991 C316.634,41.507 319.267,40.763 322.103,40.763 L379.216,40.763 L374.761,52.004 ZM289.596,80.155 C288.718,82.451 287.065,84.307 284.634,85.725 C282.204,87.143 279.435,87.851 276.331,87.851 L221.344,87.851 L238.356,40.763 L293.141,40.763 C295.706,40.763 297.832,41.591 299.520,43.244 C301.207,44.899 301.713,46.671 301.039,48.560 L289.596,80.155 ZM256.483,52.510 L247.976,76.003 L268.736,76.003 L277.141,52.510 L256.483,52.510 ZM220.837,62.535 C218.878,67.329 214.660,69.725 208.179,69.725 L206.356,69.725 L219.015,87.851 L197.344,87.851 L183.774,69.725 L173.749,69.725 L167.471,87.851 L144.787,87.851 L162.002,40.763 L218.103,40.763 C220.802,40.763 222.964,41.557 224.584,43.143 C226.204,44.730 226.777,46.636 226.306,48.864 L220.837,62.535 ZM179.217,52.510 L176.483,59.801 L200.888,59.801 L203.622,52.510 L179.217,52.510 ZM112.483,87.851 L116.737,75.902 L94.661,75.902 L90.509,87.851 L67.825,87.851 L82.104,48.560 C83.049,46.130 84.669,44.223 86.965,42.839 C89.259,41.456 92.028,40.763 95.268,40.763 L151.876,40.763 L135.167,87.851 L112.483,87.851 ZM102.965,52.712 L98.813,64.054 L120.888,64.054 L125.040,52.712 L102.965,52.712 ZM33.395,52.510 L24.788,76.105 L63.269,76.105 L59.015,87.851 L8.889,87.851 C6.256,87.851 4.130,87.025 2.509,85.370 C0.889,83.717 0.418,81.912 1.092,79.953 L12.433,48.560 C13.310,46.198 14.948,44.307 17.345,42.890 C19.740,41.472 22.525,40.763 25.699,40.763 L75.927,40.763 L71.775,52.510 L33.395,52.510 Z"
                />
              </svg>
            </Link>

            <div className="text-zinc-400 md:flex gap-2 hidden">
              <NavLink
                to="/collections"
                className={({ isActive }) =>
                  `relative px-3 py-2 flex flex-1 gap-2 items-center justify-center hover:text-zinc-100 rounded-full hover:bg-zinc-900 transition-colors ${
                    isActive ? "text-zinc-100" : ""
                  }`
                }
              >
                <Album size={20} strokeWidth={1.5} />
                <span className="text-sm font-medium leading-none">Collections</span>
              </NavLink>
              <button
                type="button"
                aria-haspopup="dialog"
                aria-expanded="false"
                aria-controls="offcanvas-filters"
                data-hs-overlay="#offcanvas-filters"
                className="relative px-3 py-2 flex flex-1 gap-2 items-center justify-center hover:text-zinc-100 rounded-full hover:bg-zinc-900 transition-colors"
              >
                <SlidersHorizontal size={20} strokeWidth={1.5} />
                <span className="text-sm font-medium leading-none">Filters</span>
                {filtering && (
                  <span className="absolute top-2 end-2 inline-flex items-center size-3 rounded-full text-xs border font-medium transform -translate-y-1/2 translate-x-1/2 bg-red-500 text-white"></span>
                )}
              </button>
              <button
                type="button"
                aria-haspopup="dialog"
                aria-expanded="false"
                aria-controls="search-modal"
                data-hs-overlay="#search-modal"
                className="relative px-3 py-2 flex flex-1 gap-2 items-center justify-center hover:text-zinc-100 rounded-full hover:bg-zinc-900 transition-colors"
              >
                <SearchIcon size={20} strokeWidth={1.5} />
                <span className="text-sm font-medium leading-none">Search</span>
              </button>
              <button
                type="button"
                aria-haspopup="dialog"
                aria-expanded="false"
                aria-controls="offcanvas-menu"
                data-hs-overlay="#offcanvas-menu"
                className="relative px-3 py-2 flex flex-1 gap-2 items-center justify-center hover:text-zinc-100 rounded-full hover:bg-zinc-900 transition-colors"
              >
                <MenuIcon size={23} strokeWidth={1.5} />
                <span className="text-sm font-medium leading-none">Menu</span>
              </button>
            </div>

            <button
              type="button"
              className="text-zinc-500 p-2 rounded-full hover:bg-zinc-900 -mr-2 hover:text-white transition-colors md:mr-0 md:hidden"
              aria-haspopup="dialog"
              aria-expanded="false"
              aria-controls="hs-subscription-with-image"
              data-hs-overlay="#hs-subscription-with-image"
            >
              <Info />
            </button>
          </div>
        </div>
      </div>

      <div className="h-16 hidden md:block"></div>

      <div className="fixed bottom-0 left-0 right-0 z-50 md:hidden">
        <div className="bg-zinc-950/85 text-zinc-400 px-3 py-2.5 flex backdrop-blur-sm items-center justify-between rounded-t-3xl">
          <NavLink
            to="/"
            className={({ isActive }) =>
              `relative px-3 py-2 rounded-2xl flex flex-1 flex-col gap-1 items-center justify-center hover:text-zinc-100 ${isActive ? "text-zinc-100" : ""}`
            }
          >
            <House size={24} strokeWidth={1.5} />
            <span className="text-[11px] leading-none">Home</span>
          </NavLink>
          <NavLink
            to="/collections"
            className={({ isActive }) =>
              `relative px-3 py-2 rounded-2xl flex flex-1 flex-col gap-1 items-center justify-center hover:text-zinc-100 ${isActive ? "text-zinc-100" : ""}`
            }
          >
            <Album size={24} strokeWidth={1.5} />
            <span className="text-[11px] leading-none">Collections</span>
          </NavLink>
          <button
            type="button"
            aria-haspopup="dialog"
            aria-expanded="false"
            aria-controls="offcanvas-filters"
            data-hs-overlay="#offcanvas-filters"
            className="mx-1 relative px-3 py-2 rounded-full text-zinc-800 flex size-14 flex-col gap-1 items-center justify-center bg-white aspect-square hover:text-zinc-950"
          >
            <SlidersHorizontal size={28} strokeWidth={1.5} />

            {filtering && (
              <span className="absolute top-2 end-2 inline-flex items-center size-3 rounded-full text-xs border font-medium transform -translate-y-1/2 translate-x-1/2 bg-red-500 text-white"></span>
            )}
          </button>
          <button
            type="button"
            aria-haspopup="dialog"
            aria-expanded="false"
            aria-controls="search-modal"
            data-hs-overlay="#search-modal"
            className="relative px-3 py-2 rounded-2xl flex flex-1 flex-col gap-1 items-center justify-center hover:text-zinc-100"
          >
            <SearchIcon size={24} strokeWidth={1.5} />
            <span className="text-[11px] leading-none">Search</span>
          </button>
          <button
            type="button"
            aria-haspopup="dialog"
            aria-expanded="false"
            aria-controls="offcanvas-menu"
            data-hs-overlay="#offcanvas-menu"
            className="relative px-3 py-2 rounded-2xl flex flex-1 flex-col gap-1 items-center justify-center hover:text-zinc-100"
          >
            <MenuIcon size={24} strokeWidth={1.5} />
            <span className="text-[11px] leading-none">Menu</span>
          </button>
        </div>
      </div>

      <div>
        <Outlet />
        <ScrollRestoration />
      </div>

      <div className="h-[75px] md:hidden"></div>

      <FiltersOffcanvas />
      <MenuOffcanvas />
      <SearchModal />
      <ModalInfoHeader />
      {location.pathname.includes("/cards/") && <ModalCardDetailProductType />}
      {location.pathname.includes("/cards/") && <ModalCardDetailNumbered />}
    </>
  );
}

export default Root;
